.main{
  display: flex;
  flex-direction: row; 
  color: blueviolet;
  box-shadow: 0 1px 15px 0 rgb(0 0 0 / 15%) !important;
}

.btn{
  text-align: right;
  margin-left: auto;
}

span{
  font-weight: bold;
}

.search{
  width: 80%;
  text-align: center;
  justify-content: center;
  margin: auto;
  margin-top: 20px;
}

.login-button{
  background: #fff;
  border: 1px solid;
  color: blueviolet;
}
.sign-button{
  background-color: blueviolet;
  border: none;
}

.bi-house-door{
  color: blueviolet;
  margin-bottom: 5px;
  margin-right: 2px;
}
.main-card{
  width: 81% !important;
}

.col-md-4{
  width: 31.333333% !important;
    margin: 7px !important;
    padding: 0px !important;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%) !important;
}
span{
  color: gray;
  font-size: 12px;
}
h5{
  color: blueviolet;
}

.bi-heart{
  float: right;
  /* margin-top: -25px; */
  margin: 4px 3px 0px 0px;
}
.bi-heart:hover{
  color: red;
}
.speces{
  display: flex;
  flex-direction: row;
  margin-right: 5px;
}
.speces p{
  margin-top: -6px;
  margin-bottom: 0rem;
}
.heartimg{
  float: right;
  border: 1px solid;
  height: 20%;
  margin-top: -27px;
  border-radius: 50%;
  width: 10%;
}

.Beds, .Bathrooms, .size{
  display: flex;
  white-space: break-spaces;
  margin-right: auto;
}

/* Filter CSS */
.nav-tabs{
  width: 78%;
  text-align: center;
  justify-content: center;
  margin: 12px 0px 10px 128px;
}
.nav-item p{
  margin-top: 0px;
  margin-bottom: -1rem;
  color: grey;
  
}
.nav-item dropdown{
  border-right: 1px solid grey;
}
.filterdropdown{
  width: 20%;
  text-align: left;
}
.filterdropdown .nav-link{
  margin-left: -16px;
  margin-top: 5px;
}
.filterul{
  box-shadow: 0 1px 15px 0 rgb(0 0 0 / 15%) !important;
  padding: 10px;
}
.searchbtn{
  height: 39px;
  border: none;
  background: blueviolet;
}
.bg-light{
  background-color: white !important;
}
.logo{
  font-weight: 700;
}
.mainmenu li{
  margin-left: 30px;
}
.activetab{
  background: #e0d0ef;
  font-size: 17px;
  padding: 1px 7px;
  color: blueviolet;
}

